<template>
  <main-slot class="container-header">
    <div class="container-header-main">
      <div class="left">
        <el-image src="/serveBotAi/images/logo.png" fit="contain" />
      </div>

      <div class="center">
        <div class="center-container">
          <div
            class="center-container-public"
            v-for="(item, index) in routerList"
            :key="item.title"
            @click="routerTo(item, index)"
          >
            {{ item.title }}
          </div>
        </div>
        <div class="center-bar">
          <div class="center-bar-hover" :style="{ left: hoverPosition }"></div>
        </div>
      </div>

      <div class="right" @click="showPanel = !showPanel">
        <div class="right-img">
          <img :src="language.img" alt="简体中文" />
        </div>
        <div class="right-title">{{ language.name }}</div>
        <div
          class="right-icon"
          :class="showPanel ? 'right-icon-down' : 'right-icon-up'"
        >
          <i class="iconfont icon-shang"></i>
        </div>
        <collapse-transition>
          <div class="right-panel" v-show="showPanel">
            <div
              class="right-panel-public"
              v-for="(item, index) in languageList"
              :key="index"
              @click="changeLanguage(item)"
            >
              <div class="">
                <img :src="item.img" alt="简体中文" />
              </div>
              <div>{{ item.name }}</div>
              <div
                :style="{
                  visibility: item.id === language.id ? 'visible' : 'hidden'
                }"
              >
                <i class="iconfont icon-gouxuan"></i>
              </div>
            </div>
          </div>
        </collapse-transition>
      </div>
    </div>
  </main-slot>
</template>

<script>
export default {
  name: "headerComp",
  components: {},
  mixins: [],
  props: {},
  computed: {
    hoverPosition() {
      const totalWidth = 100 / this.routerList.length;
      const hoverWidth = (totalWidth / 5) * 2;
      const leftPosition =
        totalWidth * this.activeIndex + (totalWidth - hoverWidth) / 2 - 1;
      return `${leftPosition}%`;
    }
  },
  watch: {},
  filters: {},
  data() {
    return {
      showPanel: false,
      activeIndex: 0,
      routerList: [
        {
          title: "首页",
          path: "/index"
        },
        {
          title: "产品说明",
          path: "/product"
        },
        {
          title: "解决方案",
          path: "/solution"
        },
        {
          title: "定价",
          path: "/price"
        },
        {
          title: "公司",
          path: "/corporation"
        }
      ],
      languageList: [
        {
          id: 0,
          img: "../../../public/images/China.png",
          lang: "zh",
          name: "简体中文"
        },
        {
          id: 1,
          img: "/images/britain.png",
          lang: "en",
          name: "English"
        },
        {
          id: 2,
          img: "/images/Japan.png",
          lang: "en",
          name: "Japan"
        }
      ],
      language: {
        id: 0,
        img: "/images/China.png",
        lang: "zh",
        name: "简体中文"
      }
    };
  },
  methods: {
    routerTo(item, index) {
      this.activeIndex = index;
      this.$router.push(item.path);
    },

    changeLanguage(item, index) {
      this.language = item;
    },
    echoChoose() {
      let path = this.$route.path;
      this.activeIndex =
        this.routerList.findIndex(item => item.path === path) || 0;
    }
  },
  created() {
    this.echoChoose();
  },
  mounted() {},
  destroyed() {}
};
</script>

