      ) !important;
      display: flex;
      flex-direction: column;
      margin-left: -$header-margin;

      &-container {
        width: 100%;
        height: calc(100% - 4px);
        display: flex;
        justify-content: space-around;
        align-items: center;
        font-size: 18px;
        color: $mainFontColor;

        &-public {
          cursor: pointer;
          flex: 1;
          text-align: center;
        }
      }

      &-bar {
        width: 100%;
        position: relative;

        &-hover {
          height: 4px;
          background: $subColor;
          width: calc(100% / 5 / 2);
          position: absolute;
          transition: left 0.3s ease;
        }
      }
    }

    .right {
      cursor: pointer;
      width: $header-right-width;
