        padding: 8px;
        background: $thirdColor; // $mainColor
        color: $mainColor; // $thirdColor
        position: absolute;
        right: calc(#{-$margin} * 2);
        top: $header-height;
        z-index: 999 !important;

        &-public {
          display: flex;
